@import "~halfmoon/css/halfmoon-variables.css";

:root {
  --dm-sidebar-link-text-color-active: #ff6767;
  --dm-sidebar-link-text-color-active-hover: #ff0000;

  --dm-navbar-link-active-text-color: #ff6767;
  --dm-navbar-link-active-text-color-hover: #ff0000;

  --lm-sidebar-link-text-color-active: #ff6767;
  --lm-sidebar-link-text-color-active-hover: #ff0000;

  --lm-navbar-link-active-text-color: #ff6767;
  --lm-navbar-link-active-text-color-hover: #ff0000;
  --lm-navbar-link-text-color-hover: rgba(0,0,0, 0.8);
  --lm-navbar-link-text-color: rgba(0,0,0, 0.65);

  --dm-navbar-link-text-color-hover: rgba(255,255,255, 0.8);
  --dm-navbar-link-text-color: rgba(255,255,255, 0.65);
  --lm-base-body-bg-color: var(--gray-color-light);

  --breadcrumb-separator-margin-left: -5px;
  --breadcrumb-separator-margin-right: 0;
  --breadcrumb-separator-content: "\e9b2";
  --tooltip-width: 15rem;
  /* Small button */

  --small-button-font-size: 1rem;
  --large-button-font-size: 1.4rem;
  --code-font-size: 1rem;
  --base-font-size: 1.1rem;
  --small-input-font-size: 1rem;
  --large-input-font-size: 1.6rem;
  --content-title-font-size: 1.4rem;
  --modal-close-font-size: 2rem;
  --alert-heading-font-size: 1.4rem;
  --alert-close-font-size: 1.4rem;
  --navbar-brand-font-size: 1.6rem;
  --sidebar-title-font-size: 1.3rem;
  --sidebar-icon-font-size: 1.3rem;
  --tooltip-font-size: 1.1rem;
  --pagination-item-font-size: 1.1rem;
  --pagination-item-height: 2.5rem;
  --pagination-item-width: 2.5rem;
  --base-html-font-size: 75%;
}

