.link-product {
  color: #000;
}

.dark-mode {
  .product:hover {
    background-color: #000!important;
  }
  .link-product {
    color: #ffffff;
  }

  .select2-container--default .select2-selection--multiple, .select2-dropdown {
    color: var(--dm-input-text-color);
    background-color: #25282B;
    border-color: var(--dm-input-border-color);
    box-shadow: var(--dm-input-box-shadow);
  }

  .select2-container--default .select2-selection--multiple .select2-selection__choice {
    background-color: black;
    &:hover {
      background-color: #25282B;
    }
  }
  .select2-container--default .select2-selection--multiple .select2-selection__choice__remove {
    height: 100% !important;
  }

  .select2-container--default .select2-selection--multiple .select2-selection__choice__remove:hover, .select2-container--default .select2-selection--multiple .select2-selection__choice__remove:focus {
    background-color: #25282B;
    color: white;
    height: 100% !important;
  }

  .select2-container--default .select2-results__option--selected {
    background-color: black;
  }
}


.link-product:hover {
  color: red!important;
}

.sidebar, .content-wrapper {
  transition: .5s
}

.dz-image > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.dropzone {
  background: inherit;
  border-radius: 5px;
  border: 1px dashed red;
  border-image: none;
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
}

.dropzone .dz-preview.dz-image-preview {
  background: inherit;
}

.object-fit-cover {
  object-fit: cover;
  object-position: center;
}

.btn-outline-danger {
  background: transparent!important;
  box-shadow: none!important;
  border: 2px solid #DE6D2D!important;
  color: #DE6D2D!important;
  font-size: 18px;
}

.btn-outline-success {
  background: transparent!important;
  box-shadow: none!important;
  border: 2px solid var(--success-color)!important;
  color: var(--success-color)!important;
  font-size: 18px;
}

.btn-outline-primary {
  background: transparent!important;
  box-shadow: none!important;
  border: 2px solid var(--primary-color)!important;
  color: var(--primary-color)!important;
  font-size: 18px;
}
.invalid-feedback {
  display: none;
}
.form-group {

  .invalid-feedback {
    display: none;
  }

  &.is-invalid {
    .invalid-feedback {
      display: block;
    }
  }
}

.wrapper-hover-image {
  cursor: pointer;
  width: 100%;

  .edit-image-hover {
    position: absolute;
    left: 50%;
    transform: translate(-50%, 50%);
    font-weight: bolder;
    bottom: 50%;
    color: white;
    font-size: 16px;
    opacity: 0;
    transition: opacity .5s ease-out;
    overflow: hidden;

    > i {
      font-size: 32px;
      text-align: center;
    }
  }

  &:hover {
    .edit-image-hover {
      opacity: 1;
    }

    img {
      transition: .5s;
      filter: brightness(50%);
    }
  }
}

.tabs {
  button {
    &.active {
      background: var(--lm-button-bg-color-hover);
      outline: none !important;
      box-shadow: none;
    }
  }
}

.dark-mode {
  .tabs {
    button.btn {

      &.active {
        background: black;
        outline: none !important;
        box-shadow: none!important;
      }
    }
  }
}

.wrapper-tabs {
  .tabs {
    margin: 0 0 20px 0 ;
    button {
      margin: 0 5px;
      &:hover, &:focus {
        outline: none !important;
        box-shadow: none!important;
      }

      &:hover, &:focus {
        outline: none !important;
        box-shadow: none!important;
      }

      &:first-child {
        margin-left: 0;
      }
    }
  }

  .tabs-content {
    transition: opacity .5s ease-in;
    opacity: 0;
    height: 0;
    overflow: hidden;

    &.active {
      opacity: 1;
      height: auto;
      transition: opacity .5s ease-out;
    }
  }
}


@-webkit-keyframes fadeIn {
  0% { opacity: 0; }
  100% { opacity: 1; }
}

@keyframes fadeIn {
  0% { opacity: 0; }
  100% { opacity: 1; }
}

@-webkit-keyframes fadeOut {
  0% { opacity: 1; }
  100% { opacity: 0; }
}

@keyframes fadeOut {
  0% { opacity: 1; }
  100% { opacity: 0; }
}

#checker_images ul {
  list-style-type: none;
}
#checker_images li {
  display: inline-block;
}
#checker_images input[type="radio"][id^="cb"] {
  display: none;
}
#checker_images label {
  border: 1px solid #fff;
  padding: 10px;
  display: block;
  position: relative;
  margin: 10px;
  cursor: pointer;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
#checker_images label::before {
  background-color: white;
  color: white;
  content: " ";
  display: block;
  border-radius: 50%;
  border: 1px solid grey;
  position: absolute;
  top: -5px;
  left: -5px;
  width: 25px;
  height: 25px;
  text-align: center;
  line-height: 28px;
  transition-duration: 0.4s;
  transform: scale(0);
}
#checker_images label img {
  height: 200px;
  width: 100%;
  object-fit: cover;
  transition-duration: 0.2s;
  transform-origin: 50% 50%;
}
#checker_images :checked+label {
  border-color: #ddd;
}
#checker_images :checked+label::before {
  content: "✓";
  background-color: grey;
  transform: scale(1);
}
#checker_images :checked+label img {
  transform: scale(0.9);
  box-shadow: 0 0 5px #333;
  z-index: -1;
}
